import React from 'react';
import {Loader as  MSLoader, Provider, teamsTheme} from '@fluentui/react-northstar'

export default function Loader(props) {
    const {loading} = props
    
    return loading?(
    <Provider theme={teamsTheme}>
      <div style={{zIndex: "1000", display:"flex", justifyContent:"center", alignItems:"center", position:"fixed", top:0, bottom:0,right:0, left:0}}>
        <MSLoader size="largest" />
      </div>
   </Provider>
  ) : null
}
